$blue: #008ED3;
$light-blue: #2aabd2;
$error: #ba0000;

body {
  padding: 50px 0 0 0;
}

#home {
  background: url('/img/background.jpg') 50% 0 repeat fixed;
  margin: 0 auto;
  width: 100%;
  max-width: 1920px;
  position: relative;
}

#home article {
  height: 458px;
  position: absolute;
  text-align: center;
  top: 150px;
  width: 100%;
}

